import React, { Component, useState, useEffect } from "react";
import { WebNav } from "../../components/navbar";
import { CoverHalf } from "../../components/coverHalf";
import { Footer } from '../../components/footer';
import { Col, Container, Row } from "react-bootstrap";
import Loader from 'react-loader-spinner'

import RAWDATA from './researchData'

function Research(params) {
  const [csvData, setCsvData] = useState([]);
  const [getData, setGetData] = useState(false);

  useEffect(() => {
    if (!getData) {
      // const url =
      //   "";
      // readRemoteFile(url, {
      //   complete: (results) => {
      //     // console.log("Results:", results);
      //     setCsvData(results.data);
      //     setGetData(true);
      //   },
      // });
      setCsvData(RAWDATA);
      // setGetData(true);
    }
  }, [getData]);

  if (getData) {
    return (
      <React.StrictMode>
        <WebNav />
        <CoverHalf />
        <ResearchSection data={csvData} />
        <Footer />
      </React.StrictMode>
    )
  } else {
    return (
      <React.StrictMode>
        <WebNav />
        <CoverHalf />
        <div className='text-center my-5'>
          <Loader type="Grid" color="#403E3E" height={80} width={80} />
        </div>
        <Footer />
      </React.StrictMode>
    )
  }
}

export { Research };

function ResearchSection(props) {
  const [showIntro, setShowIntro] = useState(true)
  const [selectProject, setSelectProject] = useState('')
  const [introData, setIntroData] = useState(props.data.research[2])

  let rawData = props.data


  const getSelectData = (projectname) => {
    let newData = {
      projectName: projectname,
      publish: '',
      purpose: '',
      challenge: '',
      method: '',
      result: '',
      application: '',
      image: '',
      imageDescription: '',
    }



    setIntroData(newData)
  }


  if (showIntro) {
    return (
      <IntroductionPage data={introData} />
    )
  }
  return (
    <div>

    </div>
  )

}


function IntroductionPage(props) {
  let data = props.data


  return (
    <React.StrictMode>
      <div className='research'>
        <div className='introPage'>
          <Container>
            <Row>
              <Col xs={12}>
                <div className='projectName'>
                  <p>{data.projectName}</p>
                </div>
                <hr />
              </Col>
            </Row>

            {
              (() => {
                if (data.publish != '') {
                  return (
                    <Row className='justify-content-md-center'>
                      <Col xs={12} md={10}>
                        <div className='description'>
                          <p className='title'>發表</p>
                          <p className='text'>{data.publish}</p>
                        </div>
                      </Col>
                    </Row>
                  )
                }
              })()
            }
            {
              (() => {
                if (data.purpose != '') {
                  return (
                    <Row className='justify-content-md-center'>
                      <Col xs={12} md={10}>
                        <div className='description'>
                          <p className='title'>目的</p>
                          <p className='text'>{data.purpose}</p>
                        </div>
                      </Col>
                    </Row>
                  )
                }
              })()
            }
            {
              (() => {
                if (data.challenge != '') {
                  return (
                    <Row className='justify-content-md-center'>
                      <Col xs={12} md={10}>
                        <div className='description'>
                          <p className='title'>挑戰</p>
                          <p className='text'>{data.challenge}</p>
                        </div>
                      </Col>
                    </Row>
                  )
                }
              })()
            }
            {
              (() => {
                if (data.method != '') {
                  return (
                    <Row className='justify-content-md-center'>
                      <Col xs={12} md={10}>
                        <div className='description'>
                          <p className='title'>方法</p>
                          <p className='text'>{data.method}</p>
                        </div>
                      </Col>
                    </Row>
                  )
                }
              })()
            }
            {
              (() => {
                if (data.result != '') {
                  return (
                    <Row className='justify-content-md-center'>
                      <Col xs={12} md={10}>
                        <div className='description'>
                          <p className='title'>結果</p>
                          <p className='text'>{data.result}</p>
                        </div>
                      </Col>
                    </Row>
                  )
                }
              })()
            }
            {
              (() => {
                if (data.application != '') {
                  return (
                    <Row className='justify-content-md-center'>
                      <Col xs={12} md={10}>
                        <div className='description'>
                          <p className='title'>應用</p>
                          <p className='text'>{data.application}</p>
                        </div>
                      </Col>
                    </Row>
                  )
                }
              })()
            }
            <Row className='justify-content-md-center'>
              <Col xs={12} md={10}>
                <div className='image'>
                  <p className='title'>示意圖</p>
                  <img src={data.image} />
                  <p className='text'>{data.imageDescription}</p>
                </div>
              </Col>
            </Row>


          </Container>
        </div>

      </div>
    </React.StrictMode>
  )
}
